.vertical-menu{
	position: relative;
	width: 352px;
	border:1px solid #0002;
	border-radius: 5px;
}

.payment-content {
	display: flex;
	position: relative;
	flex-direction: column;
	width: 100%;
	max-width: 1080px;
	min-width: 1080px;
	min-height: 669px;
	margin: 28px auto;
}

.payment-container .container-left {
	width: 352px;
	display: flex;
	position: relative;
	background: #FFFFFF 0% 0% no-repeat padding-box;
}

.payment-container .container-right {
	border:1px solid #0002;
	display: flex;
	position: relative;
	width: 100%;
	flex: 1;
	margin-left: 16px;
	border-radius: 5px;
	background: #fff;
}

.payment-container .container-right .payment {
	flex-direction: column;
	justify-content: center;
	padding: 65px 16px 40px 92px;
	max-width: 520px;
	min-height: 450px;
	width: 100%;
}

.payment-container .container-right .payment img {
	display: block;
}

.payment-container .container-right .payment .payment-title {
	position: relative;
	margin: 20px 0 40px 0;
	height: 37px;
	text-align: left;
	font: normal normal 600 24px/32px Montserrat;
	letter-spacing: 0px;
	color: #1D1F22;
	opacity: 1;
}

.btn-payment-submit{
	width: 100%;
	height: 59px;
	background-color: #5754F7;
	border-radius: 5px;
	text-align: center;
	font: normal normal 600 20px/25px Montserrat;
	letter-spacing: 0px;
	color: #FFFFFF;
	opacity: 1;
	border: none;
	position: relative;
	margin-top: 133px;
	letter-spacing: 0;
	cursor: pointer;
}

.payment-container input{
	font: normal normal 500 18px/25px Montserrat;
}

.payment-image {
	position: relative;
	justify-content: center;
	align-items: center;
	display: flex;
	height: 192px;
	overflow: hidden;
	flex-direction: column;
	max-width: 192px;
	margin: 20px auto auto;
}

.payment-image img{
	position: relative;
	display: flex;
	max-width: 100%;
	max-height: 100%;
}

.payment-name{
	margin-top: 16px;
	text-align: center;
}

.payment-name span{
	font: normal normal 600 24px/34px Montserrat;
	letter-spacing: 0px;
	color: #2B2943;
}

.separation{
	height: 2px;
	background: #E0E0E0 0% 0% no-repeat padding-box;
	opacity: 1;
	margin: 22px 20px 20px 20px;
}

.payment-settings a.active{
	text-decoration: none;
	position: relative;
	color: #FFFFFF;
  padding: 0;
	font: normal normal 600 22px/29px Montserrat;
	background: #1473E6 0% 0% no-repeat padding-box;
	border-radius: 5px;

}

.payment-settings a{
	margin: 0 20px;
	height: 56px;
	display: block;
	text-align: left;
	font: normal normal 500 22px/27px Montserrat;
	letter-spacing: 0px;
	text-decoration: none;
	color: #000;
}

.payment-settings a:hover{
	text-decoration: underline;
}

.payment-settings a span{
	top: 12px;
	left: 17px;
	position: relative;
}

.payment-container select{
	color: #000;
	font: normal normal 500 18px/25px Montserrat;
}

.btn-back{
	display: flex;
	width: 168px;
	border: 1px solid #85B4EE;
	color:#000;
	border-radius: 10px;
	opacity: 1;
	margin-bottom: 40px;
	cursor: pointer;
	text-decoration: none;
}
.btn-back:hover{
	text-decoration: underline
}

.payment-container {
	background-color: #f7f7f7;
	background-repeat: repeat;
	background-size: 100%;
	background-attachment: fixed;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	position: fixed;
	display: flex;
	overflow: auto;
}

.payment-container .cl{
	display: flex;
}

#card{
	width: 14px;
	height: 14px;
}

.payment-label-1{
	margin-left: 10px;
}

.payment-label-2{
	position: relative;
	display: block;
	margin-top: 36px;
}

.payment-label-3{
	position: relative;
	display: block;
	margin-top: 18px;
}

.payment select,.payment input{
	background-color: #F7F7F7;
}

.payment-cardExpirationDate-cardSecretCode {
	display: flex;
	flex-direction: row;
	position: relative;
	margin-top: 18px;
}

.payment-label-1,.payment-label-2,.payment-label-3,.payment-label-4,.payment-label-5{
	font: normal normal 600 16px/21px Montserrat;
}

.payment-cardExpirationDate {
	position: relative;
	display: flex;
	flex-direction: column;
	width: calc(100% - 10px);
	margin-right: 10px;
}

.payment-cardSecretCode {
	position: relative;
    display: flex;
    flex-direction: column;
    width: calc(100% - 10px);
    margin-left: 10px;
}